/**
 * Twilio Media Stream WebSocket Handler - OpenAI Realtime API Integration
 * WebSocket /api/webhooks/twilio/voice-stream
 *
 * This WebSocket endpoint receives bidirectional audio streams from Twilio
 * and pipes them to/from OpenAI Realtime API for real-time conversation.
 *
 * Architecture Flow:
 * 1. Twilio connects WebSocket with audio stream (Î¼-law, 8kHz, mono)
 * 2. We establish WebSocket to OpenAI Realtime API
 * 3. Audio flows: Twilio â†’ Us â†’ OpenAI (user speaking)
 * 4. Audio flows: OpenAI â†’ Us â†’ Twilio (AI responding)
 * 5. Transcript stored in Firestore for compliance
 * 6. Call outcome analyzed and stored when call completes
 *
 * OpenAI Realtime API:
 * - Model: gpt-4o-realtime-preview-2024-12-17
 * - Audio format: G.711 Î¼-law (matches Twilio)
 * - Voice: "alloy" (professional female)
 * - Turn detection: Server VAD (Voice Activity Detection)
 * - Modalities: text + audio
 *
 * CRITICAL: This is a WebSocket route, not HTTP REST
 * Next.js does not natively support WebSocket routes
 * 
 * OPTIONS FOR IMPLEMENTATION:
 * 1. Deploy separate WebSocket server (Node.js + ws library)
 * 2. Use Vercel Edge Functions with Durable Objects (enterprise)
 * 3. Use external WebSocket service (e.g., Pusher, Ably)
 * 4. Use API Route with Server-Sent Events + HTTP/2 (workaround)
 *
 * THIS FILE IS A SCAFFOLD/DOCUMENTATION
 * Actual WebSocket implementation should be in separate Node.js server
 * or deployed as serverless WebSocket function (AWS Lambda + API Gateway WebSocket API)
 *
 * For MVP: Store this file as documentation and implement WebSocket server separately
 */

import { NextRequest } from 'next/server';

/**
 * IMPORTANT: Next.js API Routes do not support WebSocket connections
 * 
 * To implement this feature, you need to:
 * 
 * OPTION 1: Deploy Separate WebSocket Server
 * ```bash
 * # Create standalone WebSocket server
 * mkdir websocket-server
 * cd websocket-server
 * npm init -y
 * npm install ws openai twilio firebase-admin
 * 
 * # Create server.js
 * const WebSocket = require('ws');
 * const OpenAI = require('openai');
 * // See implementation below
 * ```
 * 
 * OPTION 2: Use AWS Lambda + API Gateway WebSocket API
 * - Deploy Lambda function with WebSocket support
 * - Configure API Gateway with $connect, $disconnect, $default routes
 * - Use Lambda layers for dependencies
 * 
 * OPTION 3: Use Vercel Edge Functions (Limited)
 * - Vercel supports WebSocket connections in Edge Functions
 * - But has limitations on connection duration and size
 * 
 * RECOMMENDED: Option 1 (Separate WebSocket Server)
 * - Deploy to Railway.app, Fly.io, or AWS EC2
 * - Most control and flexibility
 * - Can handle long-duration calls
 */

export async function GET(_req: NextRequest) {
  return new Response(
    JSON.stringify({
      error: 'WebSocket endpoint not implemented',
      message: 'This endpoint requires a separate WebSocket server. See code comments for implementation options.',
      documentation: 'This file serves as documentation for the required WebSocket implementation.',
      recommendedApproach: 'Deploy separate Node.js WebSocket server with ws + openai libraries',
    }),
    {
      status: 501,
      headers: { 'Content-Type': 'application/json' },
    }
  );
}

/**
 * REFERENCE IMPLEMENTATION (for separate WebSocket server)
 * 
 * This is the code you should use in your standalone WebSocket server
 * Deploy this to Railway.app, Fly.io, or AWS EC2
 * 
 * File: websocket-server/server.js
 */

const REFERENCE_IMPLEMENTATION = `
const WebSocket = require('ws');
const OpenAI = require('openai');
const admin = require('firebase-admin');

// Initialize Firebase Admin
admin.initializeApp({
  credential: admin.credential.cert({
    projectId: process.env.FIREBASE_PROJECT_ID,
    clientEmail: process.env.FIREBASE_CLIENT_EMAIL,
    privateKey: process.env.FIREBASE_PRIVATE_KEY.replace(/\\\\n/g, '\\n'),
  }),
});

const db = admin.firestore();
const openai = new OpenAI({ apiKey: process.env.OPENAI_API_KEY });

// Create WebSocket server
const wss = new WebSocket.Server({ port: process.env.WS_PORT || 8080 });

console.log('ðŸŽ™ï¸  WebSocket server running on port', process.env.WS_PORT || 8080);

wss.on('connection', async (twilioWs, req) => {
  console.log('ðŸ“ž Twilio connected');

  // Parse context from query parameter
  const url = new URL(req.url, \`http://\${req.headers.host}\`);
  const contextParam = url.searchParams.get('context');
  
  if (!contextParam) {
    console.error('âŒ Missing context parameter');
    twilioWs.close();
    return;
  }

  const context = JSON.parse(decodeURIComponent(contextParam));
  
  let streamSid = null;
  let callSid = null;
  let openaiWs = null;
  let transcript = [];
  let audioBuffer = [];

  try {
    // Connect to OpenAI Realtime API
    openaiWs = new WebSocket('wss://api.openai.com/v1/realtime?model=gpt-4o-realtime-preview-2024-12-17', {
      headers: {
        'Authorization': \`Bearer \${process.env.OPENAI_API_KEY}\`,
        'OpenAI-Beta': 'realtime=v1',
      },
    });

    openaiWs.on('open', () => {
      console.log('ðŸ¤– OpenAI Realtime API connected');

      // Configure OpenAI session
      openaiWs.send(JSON.stringify({
        type: 'session.update',
        session: {
          modalities: ['text', 'audio'],
          instructions: context.aiInstructions,
          voice: 'alloy', // Professional female voice
          input_audio_format: 'g711_ulaw', // Twilio format
          output_audio_format: 'g711_ulaw',
          input_audio_transcription: {
            model: 'whisper-1', // Transcribe user input
          },
          turn_detection: {
            type: 'server_vad', // Voice Activity Detection by OpenAI
            threshold: 0.5,
            prefix_padding_ms: 300,
            silence_duration_ms: 500,
          },
          temperature: 0.7, // Slightly creative but consistent
          max_response_output_tokens: 4096,
        },
      }));

      // Send initial context to AI
      openaiWs.send(JSON.stringify({
        type: 'conversation.item.create',
        item: {
          type: 'message',
          role: 'system',
          content: [{
            type: 'input_text',
            text: \`You are calling \${context.recipientName} about invoice \${context.invoiceReference} for Â£\${context.amount}, which is \${context.daysPastDue} days overdue. Business name: \${context.businessName}.\`,
          }],
        },
      }));

      openaiWs.send(JSON.stringify({ type: 'response.create' }));
    });

    // Handle messages from OpenAI
    openaiWs.on('message', (data) => {
      try {
        const message = JSON.parse(data.toString());

        // Forward audio from OpenAI to Twilio
        if (message.type === 'response.audio.delta' && message.delta) {
          if (streamSid) {
            twilioWs.send(JSON.stringify({
              event: 'media',
              streamSid,
              media: {
                payload: message.delta, // Base64 encoded Î¼-law audio
              },
            }));
          }
        }

        // Capture transcript from OpenAI
        if (message.type === 'response.audio_transcript.delta' && message.delta) {
          transcript.push({
            role: 'assistant',
            text: message.delta,
            timestamp: new Date().toISOString(),
          });
        }

        if (message.type === 'conversation.item.input_audio_transcription.completed') {
          transcript.push({
            role: 'user',
            text: message.transcript,
            timestamp: new Date().toISOString(),
          });
        }

        // Handle errors from OpenAI
        if (message.type === 'error') {
          console.error('âŒ OpenAI error:', message.error);
        }

        // Log function calls if AI wants to send payment link
        if (message.type === 'response.function_call_arguments.done') {
          console.log('ðŸ”§ Function call:', message.name, message.arguments);
          // TODO: Implement function to send SMS payment link
        }

      } catch (err) {
        console.error('âŒ Error processing OpenAI message:', err);
      }
    });

    openaiWs.on('error', (error) => {
      console.error('âŒ OpenAI WebSocket error:', error);
    });

    openaiWs.on('close', () => {
      console.log('ðŸ¤– OpenAI connection closed');
    });

    // Handle messages from Twilio
    twilioWs.on('message', (data) => {
      try {
        const message = JSON.parse(data.toString());

        switch (message.event) {
          case 'start':
            streamSid = message.start.streamSid;
            callSid = message.start.callSid;
            console.log(\`ðŸ“ž Stream started: \${streamSid}\`);
            break;

          case 'media':
            // Forward audio from Twilio to OpenAI
            if (openaiWs && openaiWs.readyState === WebSocket.OPEN) {
              openaiWs.send(JSON.stringify({
                type: 'input_audio_buffer.append',
                audio: message.media.payload, // Base64 encoded Î¼-law audio
              }));
            }
            break;

          case 'stop':
            console.log('ðŸ“ž Stream ended');
            
            // Save transcript and call outcome to Firestore
            saveCallOutcome(context, callSid, transcript).catch(console.error);
            
            if (openaiWs) {
              openaiWs.close();
            }
            break;
        }
      } catch (err) {
        console.error('âŒ Error processing Twilio message:', err);
      }
    });

    twilioWs.on('error', (error) => {
      console.error('âŒ Twilio WebSocket error:', error);
    });

    twilioWs.on('close', () => {
      console.log('ðŸ“ž Twilio disconnected');
      if (openaiWs) {
        openaiWs.close();
      }
    });

  } catch (error) {
    console.error('âŒ Connection error:', error);
    twilioWs.close();
    if (openaiWs) {
      openaiWs.close();
    }
  }
});

/**
 * Save call transcript and outcome to Firestore
 */
async function saveCallOutcome(context, callSid, transcript) {
  try {
    console.log('ðŸ’¾ Saving call outcome to Firestore');

    // Find collection attempt by callSid
    const attemptsSnapshot = await db
      .collection('collection_attempts')
      .where('callSID', '==', callSid)
      .limit(1)
      .get();

    if (attemptsSnapshot.empty) {
      console.error('âŒ Collection attempt not found for callSid:', callSid);
      return;
    }

    const attemptDoc = attemptsSnapshot.docs[0];
    
    // Combine transcript into text
    const fullTranscript = transcript
      .map(t => \`[\${t.role.toUpperCase()}] \${t.text}\`)
      .join('\\n');

    // Use OpenAI to analyze transcript and extract outcome
    const analysis = await openai.chat.completions.create({
      model: 'gpt-4o-mini',
      messages: [
        {
          role: 'system',
          content: \`Analyze this debt collection call transcript and extract structured data.
Return JSON with:
- outcome: 'paid' | 'promise' | 'partial' | 'refused' | 'no_answer' | 'voicemail' | 'dispute' | 'financial_hardship' | 'error'
- summary: 2-3 sentence summary of call
- nextAction: 'accept_partial' | 'schedule_followup' | 'escalate' | 'pause' | 'complete' | 'agency_handoff'
- scheduledFollowupDate: if debtor promised to pay by date (YYYY-MM-DD)
- partialAmountAgreed: if partial payment agreed (number)
- clientProposedDate: if client suggested payment date (YYYY-MM-DD)
- disputeDetails: if debt was disputed, what are the details
- paymentMethod: 'sms_link' | 'ivr' | 'bank_transfer'
- consentGiven: did caller consent to recording (boolean)\`,
        },
        {
          role: 'user',
          content: fullTranscript,
        },
      ],
      response_format: { type: 'json_object' },
    });

    const outcome = JSON.parse(analysis.choices[0].message.content);

    // Update Firestore
    await attemptDoc.ref.update({
      callTranscript: fullTranscript,
      callNotes: outcome.summary,
      callOutcome: outcome.outcome,
      nextAction: outcome.nextAction,
      clientProposedDate: outcome.clientProposedDate || null,
      partialAmountAgreed: outcome.partialAmountAgreed || null,
      paymentMethod: outcome.paymentMethod || null,
      consentGiven: outcome.consentGiven !== false, // Default to true unless explicitly refused
      callEndedAt: admin.firestore.FieldValue.serverTimestamp(),
      result: outcome.outcome === 'paid' || outcome.outcome === 'promise' || outcome.outcome === 'partial' ? 'success' : 'failed',
      updatedAt: admin.firestore.FieldValue.serverTimestamp(),
    });

    console.log('âœ… Call outcome saved:', outcome.outcome);

  } catch (error) {
    console.error('âŒ Error saving call outcome:', error);
  }
}

// Handle graceful shutdown
process.on('SIGTERM', () => {
  console.log('âš ï¸  SIGTERM received, closing WebSocket server');
  wss.close(() => {
    console.log('ðŸ‘‹ WebSocket server closed');
    process.exit(0);
  });
});
`;

/**
 * DEPLOYMENT INSTRUCTIONS
 * 
 * 1. Create new directory for WebSocket server:
 *    mkdir websocket-server
 *    cd websocket-server
 * 
 * 2. Initialize Node.js project:
 *    npm init -y
 * 
 * 3. Install dependencies:
 *    npm install ws openai firebase-admin
 * 
 * 4. Create server.js with the REFERENCE_IMPLEMENTATION code above
 * 
 * 5. Create .env file:
 *    FIREBASE_PROJECT_ID=your-project-id
 *    FIREBASE_CLIENT_EMAIL=...
 *    FIREBASE_PRIVATE_KEY="-----BEGIN PRIVATE KEY-----..."
 *    OPENAI_API_KEY=sk-proj-...
 *    WS_PORT=8080
 * 
 * 6. Deploy to Railway.app:
 *    - railway login
 *    - railway init
 *    - railway up
 *    - railway variables set (add all env vars)
 *    - railway domain (get public URL)
 * 
 * 7. Update Relay environment:
 *    TWILIO_WEBSOCKET_URL=wss://your-railway-url.railway.app
 * 
 * 8. Update voice-ai webhook to use Railway URL:
 *    const streamUrl = `${process.env.TWILIO_WEBSOCKET_URL}/voice-stream?context=${streamContext}`;
 * 
 * ALTERNATIVE: AWS Lambda + API Gateway WebSocket API
 * - More complex but fully serverless
 * - Better for production scale
 * - See AWS documentation for WebSocket Lambda setup
 */

export { REFERENCE_IMPLEMENTATION };
